.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #f3f3f3;
}

.wrapper {
  width: 90%;
  margin: 0 auto;
  text-align: center;
}

.wrapper h1 {
  font-size: 50px;
  font-family: 'Oswald', sans-serif;
  color: #111;
  letter-spacing: 4px;
}

.dataCollection {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 10px;
}

.dataCollection :nth-child(1){
  border-bottom-color: rgba(0, 0, 255, 0.5);
}

.dataCollection :nth-child(2){
  border-bottom-color: rgba(0, 255, 0, 0.5);
}

.dataCollection :nth-child(3){
  border-bottom-color: rgba(255, 0, 0, 0.5);
}

.cardContainer {
  background-color: #fff;
  border-bottom-style: solid;
  border-bottom-width: 8px;
  box-shadow: 2px 2px 2px 2px #e8e8e8;
  font-size: 30px;
  color: #111;
  font-family: 'Roboto', sans-serif;
  padding: 20px;
  text-align: left;
}

.cardContainer h3, h2{
  font-size: 15px;
}

.cardContainer h3 {
  color: rgba(0, 0, 0, 0.4);
}

select{
  margin: 20px 0;
}

.graphs {
  padding-bottom: 40px;
}

@media only screen and (min-width: 1000px) and (max-width: 1439px){

  .wrapper{
    width: 80%;
  }

  .dataCollection {
    width: 70%;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 20px;
  }

  select{
    width: 40%;
  }

  .graphs {
    width: 70%;
    margin: 0 auto;
  }

}

@media only screen and (min-width: 1440px){
  .wrapper{
    width: 90%;
  }

  .dataCollection {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 40px;
  }

  select {
    width: 40%;
    margin: 50px 0;
  }
}